import * as React from "react";
import { useRef } from "react";
import { motion } from "framer-motion";
import "./FloatingElement.css";

export const FloatingElement = () => {
  const constraintsRef = useRef(null);

  return (
    <div className='container-for-ball'>
      <motion.div className="container" ref={constraintsRef}>
        <motion.div className="item" drag dragConstraints={constraintsRef}>
          <p className='textWithinCircle'>To be updated</p>
        </motion.div>
        {/* <p className='textBelowCircle'>Tap and drag circle</p> */}
      </motion.div>
    </div>
  );
};

export default FloatingElement;
